import { Component, h, Element, State, Host, Listen } from '@stencil/core';
import { ICellRendererComp, ICellRendererParams } from 'ag-grid-community';
import { LabelPosition } from '../../../at-toggle-switch/at-toggle-switch';
import { ITooltipCell } from '../../../../types/table-cells';

export interface ToggleCellData {
    value: boolean;
    label?: string;
    labelPosition?: 'before' | 'after';
    disabled?: boolean;
}

export interface IToggleCellParams extends ICellRendererParams, ITooltipCell {
    toggleValue?: (data: any) => boolean;
    label?: (data: any) => string;
    labelPosition?: (data: any) => LabelPosition;
    onTrigger?: (params: ICellRendererParams) => void;
    disabled?: (data: any) => boolean;
}

/**
 * @category Data Tables
 * @description A cell component for displaying a toggle.
 */

@Component({
    tag: 'at-toggle-cell',
    shadow: false,
})
export class AtToggleCell implements ICellRendererComp {
    @Element() el: HTMLElement;
    @State() params: IToggleCellParams;
    @State() label: string;
    @State() labelPosition: LabelPosition;
    @State() disabled: boolean;
    @State() value: boolean;
    timer: any;

    init(params: ICellRendererParams): void {
        this.params = params as IToggleCellParams;

        if (this.params.toggleValue) {
            this.value = this.params.toggleValue(params.data);
        } else {
            this.value = params.data?.toggleCell?.value || false;
        }

        if (this.params.label) {
            this.label = this.params.label(params.data);
        } else {
            this.label = params.data?.toggleCell?.label || '';
        }

        this.labelPosition = params.data?.toggleCell?.labelPosition || 'after';
        this.disabled = params.data?.toggleCell?.disabled || false;
    }

    getGui(): HTMLElement {
        return this.el;
    }

    refresh(params: ICellRendererParams): boolean {
        this.updateCell(params.data);
        return true;
    }

    @Listen('toggleFunction')
    toggleHandler(change: CustomEvent) {
        clearTimeout(this.timer);
        this.params.value = change.detail;
        this.timer = setTimeout(() => {}, 2000);
    }

    private updateCell(newValue: any) {
        clearTimeout(this.timer);
        this.params.value = newValue;
        this.timer = setTimeout(() => {}, 2000);
    }

    render() {
        return (
            <Host class="flex h-full items-center leading-[100%]">
                <at-toggle-switch
                    label={this.label}
                    onChange={() => this.params.onTrigger?.(this.params)}
                    label_position={this.labelPosition}
                    show_label={!!this.label}
                    value={this.value}
                    disabled={this.disabled}
                ></at-toggle-switch>
            </Host>
        );
    }
}
